import {SimpleFilterOption} from '../simple-filter/simple-filter.models';
import {AdaptTreeNodeTyped} from '../../tree/index';

// TODO: Refactor this if possible
/* eslint-disable-next-line @typescript-eslint/no-explicit-any */
export interface FacetedFilterOption<T = any> extends SimpleFilterOption<T> {
  values: SimpleFilterOption<T>[];
}

export interface FacetedFilterTexts {
  initialDropdownAnchorLabel?: string;
  initialDropdownAnchorWithCounterLabel?: string;
  /** @deprecated since v12.13.0 */
  popoverTitleLabel?: string;
  clearButtonLabel?: string;
  selectedCountLabel?: string;
  hiddenItemsCounterLabel?: string;
}

export type FacetedFilterTreeNode = AdaptTreeNodeTyped<FacetedFilterOption | SimpleFilterOption>;

export interface SelectedFacetedFilterTag {
  text: string;
  nodes: FacetedFilterTreeNode[];
  parent: FacetedFilterTreeNode;
}
